// DOM Elements
const loanAmount = document.getElementById('amount');
const interestRate = document.getElementById('interest');
const monthToPay = document.getElementById('month');
const monthlyPayment = document.getElementById('payment');
const inputs = [loanAmount, interestRate, monthToPay];
let loanValidity, interestValidty, monthValidity = false;
inputs.forEach(i => calculatePayment(i));
window.onload = function () {
    loanAmount.focus();
}
function calculatePayment(input) {
    input.addEventListener('input', function (e) {
        let loan = Number(loanAmount.value);
        let interest = Number(interestRate.value);
        let month = Number(monthToPay.value);
        switch (e.target) {
            case loanAmount:
                handleHelp(loanAmount);
                break;
            case interestRate:
                handleHelp(interestRate);
                break;
            case monthToPay:
                handleHelp(monthToPay);
                break;
        }
        function handleHelp(currentInput) {
            if (validate(currentInput)) {
                showHelp(currentInput, false);
                showPayment();
                if (loanValidity && interestValidty && monthValidity) {
                    let payment = month === 0 ? loan + interest : (loan + interest) / month;
                    monthlyPayment.textContent = payment.toFixed(3);
                }
            }
            else {
                showHelp(currentInput, true);
            }
        }
    })
}
function showPayment() {
    loanValidity = validate(loanAmount) ? true : false;
    interestValidty = validate(interestRate) ? true : false;
    monthValidity = validate(monthToPay) ? true : false;

}
function validate(input) {
    let validity = false;
    if (!input.value.startsWith('0') && input.validity.valid) {
        if (!isNaN(Number(input.value))) {
            if (Number(input.value) >= 0)
                validity = true;
        }
    }
    else {
        validity = false;
    }
    return validity;
}
function showHelp(element, help = false) {
    let helpBlock = element.closest('label').querySelector('small');
    help ? ClassList(helpBlock).remove('none') : ClassList(helpBlock).add('none');
}

function ClassList(element) {
    let list = element.classList;
    return {
        add: function (cls) { list.add(cls); return this },
        remove: function (cls) { list.remove(cls); return this }
    }
}
